<template>
  <v-container fluid fill-height>
    <v-row align="center" justify="center">
      <v-card width="80%" >
        <v-row align="center" justify="center">
          <v-col md="4" style="padding: 0px" class="text-center">
            <v-img
              src="../assets/add_bg2.png"
              height="655px"
              width="100%"
              class="align-center"
            >
              <h1 class="white--text" style="font-size: 3.5em !important">
                ¿NOVEDADES?
              </h1>
            </v-img>
          </v-col>
          <v-col md="8" >
            <v-form>
              <v-row justify="center" class="mt-7">
                <h1 class="title">AÑADIR MOVIMIENTO</h1>
              </v-row>
              <v-row justify="center" class="mt-10">
                <v-btn
                  class="but white--text"
                  :color="color2"
                  large
                  style="margin-right: 15px"
                  v-model="pressedI"
                  @click="
                    pressedI = true;
                    pressedG = false;
                    movementType = 'Ingreso';
                    switchI();
                  "
                >
                  INGRESO
                  <v-icon style="margin-right: -9px">mdi-chevron-up </v-icon>
                </v-btn>
                <v-btn
                  class="but white--text"
                  :color="color1"
                  large
                  style="margin-left: 15px"
                  v-model="pressedG"
                  @click="
                    pressedI = false;
                    pressedG = true;
                    movementType = 'Gasto';
                    switchG();
                  "
                >
                  GASTO
                  <v-icon style="margin-right: -9px">mdi-chevron-down </v-icon>
                </v-btn>
              </v-row>
              <v-row justify="center">
                <v-col md="6" class="text-center">
                  <v-text-field
                    id="movementConcept"
                    v-model="movementConcept"
                    label="Concepto"
                    name="movementConcept"
                    class="customText"
                    type="text"
                    required
                    :error-messages="conceptErrors"
                    @input="$v.movementConcept.$touch()"
                    @blur="$v.movementConcept.$touch()"
                  />
                </v-col>
                <v-col md="6" class="text-center">
                  <v-text-field
                    id="movementAmount"
                    v-model="movementAmount"
                    label="Importe (€)"
                    name="movementAmount"
                    class="customText"
                    type="number"
                    required
                    :error-messages="amountErrors"
                    @input="$v.movementAmount.$touch()"
                    @blur="$v.movementAmount.$touch()"
                  />
                </v-col>
              </v-row>
              <v-row justify="center" align="center">
                <v-col class="text-center" md="6">
                  <v-btn
                    class="but white--text"
                    :color="color3"
                    large
                    style="margin-right: 15px"
                    v-model="pressedU"
                    @click="
                      pressedU = true;
                      pressedP = false;
                      switchU();
                    "
                  >
                    PUNTUAL
                  </v-btn>
                  <v-btn
                    class="but white--text"
                    :color="color4"
                    large
                    style="margin-left: 15px"
                    v-model="pressedP"
                    @click="
                      pressedU = false;
                      pressedP = true;
                      switchP();
                    "
                  >
                    PERIÓDICO
                  </v-btn>
                </v-col>
                <v-col
                  ><v-select
                    required
                    label="Categoría"
                    class="customText"
                    v-model="movementCategory"
                    :items="categories"
                    :error-messages="categoryErrors"
                    @input="$v.movementCategory.$touch()"
                    @blur="$v.movementCategory.$touch()"
                  ></v-select
                ></v-col>
              </v-row>

              <v-row>
                <v-col justify="center" align="center">
                  <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        class="customText"
                        v-model="initialDate"
                        label="Fecha de Inicio"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="initialDate"
                      @input="menu2 = false"
                      :landscape="true"
                    ></v-date-picker>
                  </v-menu>
                </v-col>
                <v-col justify="center" align="center">
                  <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-bind:class="{
                          customText: !isUnique,
                          disabledText: isUnique,
                        }"
                        :disabled="isUnique"
                        v-model="finalDate"
                        label="Fecha de Fin (Opcional)"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      :disabled="isUnique"
                      v-model="finalDate"
                      @input="menu1 = false"
                      :landscape="true"
                      :min="initialDate"
                    ></v-date-picker>
                  </v-menu>
                </v-col>
              </v-row>
              <v-row justify="center">
                <v-col md="6" class="d-flex justify-space-around">
                  <v-textarea
                    class="customText"
                    outlined
                    name="movementComment"
                    label="Comentario"
                    v-model="movementComment"
                    no-resize
                  ></v-textarea>
                </v-col>
                <v-col md="6" style="padding-top: 0px">
                  <v-row justify="center" align="center">
                    <v-col>
                      <v-text-field
                        v-bind:class="{
                          customText: !isUnique,
                          disabledText: isUnique,
                        }"
                        class="dis disLeft"
                        :disabled="isUnique"
                        id="movementFreq"
                        v-model="movementFreq"
                        label="Frecuencia"
                        name="movementFreq"
                        type="number"
                        :error-messages="frequencyErrors"
                        @input="$v.movementFreq.$touch()"
                        @blur="$v.movementFreq.$touch()"
                      />
                    </v-col>
                    <v-col>
                      <div></div>
                      <v-select
                        v-bind:class="{
                          customText: !isUnique,
                          disabledText: isUnique,
                        }"
                        class="dis disRight"
                        :disabled="isUnique"
                        label="Unidad de frecuencia"
                        v-model="freqUnit"
                        :items="freqUnits"
                        :error-messages="freqUnitErrors"
                        @input="$v.freqUnit.$touch()"
                        @blur="$v.freqUnit.$touch()"
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row justify="center" align="center">
                    <v-col class="text-center">
                      <v-btn
                        class="but white--text"
                        style="margin-right: 30px"
                        color="primary"
                        large
                        @click="clear"
                        >BORRAR TODO</v-btn
                      >
                      <v-btn
                        class="but white--text"
                        color="primary"
                        style="margin-left: 10px"
                        large
                        @click="confirm"
                        >CONFIRMAR</v-btn
                      >
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-form>
          </v-col>
        </v-row>
      </v-card>
      <v-btn
        fab
        dark
        large
        color="primary"
        fixed
        right
        bottom
        class="mb-14"
        style="font-size:2em"
        @click="dialog = true"
        >?</v-btn
      >

      <Help title="AÑADIR MOVIMIENTO" v-model="dialog" />
    </v-row>
  </v-container>
</template>

<style scoped>

.disabledText {
  width: 100% !important;
  padding-left: 40px;
  padding-right: 40px;
}


.title {
  font-size: 2.5em !important;
  color: #13314a !important;
}

.but {
  font-size: 1em;
}

.customText {
  width: 100% !important;
  padding-left: 40px;
  padding-right: 40px;
}

.customText >>> .v-label {
  color: #13314a;
  font-size: 1.1em;
}

.customText >>> input {
  font-size: 1.2em;
}

.customText >>> .v-icon {
  color: #13314a;
}

.dis {
  padding-right:0px;
  padding-left: 0px;
}

.disLeft {
  padding-left: 40px !important;
  padding-right: 5px !important;
}

.disRight {
  padding-right: 40px !important;
  padding-left: 5px !important;
}


</style>

<script>
import { mapState, mapMutations } from "vuex";

import Help from "../components/Help";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minValue,
  minLength,
  email,
  alphaNum,
  decimal,
  requiredIf,
  numeric,
  integer,
} from "vuelidate/lib/validators";
const twoDecimals = (value) => (value * 100) % 1 == 0;

export default {
  name: "AddMovement",
  components: { Help },
  mixins: [validationMixin],
  validations: {
    movementConcept: { required, maxLength: maxLength(20) },
    movementAmount: {
      required,
      decimal,
      minAmount: function(value) {
        return value >= 0.01;
      },
      twoDecimals,
    },
    movementFreq: {
      required: requiredIf(function(frequency) {
        return this.isUnique == false;
      }),
      integer,
      minFreq: function(value) {
        return value > 0;
      },
    },
    movementCategory: { required },
    freqUnit: {
      required: requiredIf(function(frequency) {
        return this.isUnique == false;
      }),
    },
    movementType: { required },
    initialDate: { required },
    finalDate: {
      isAfter: function(date) {
        var initial = new Date(this.initialDate);
        var final = new Date(date);
        return final > initial;
      },
    },
  },
  data() {
    return {
      dialog: false,
      color1: "#445e73",
      color2: "#13314a",
      color3: "#445e73",
      color4: "#13314a",
      isUnique: true,
      pressedI: false,
      pressedG: true,
      pressedP: false,
      pressedU: true,
      freqUnits: ["Día", "Semana", "Mes", "Año"],
      categories: ["Ocio", "Servicios", "Compras", "Ingresos", "Otro"],
      initialDate: new Date().toISOString().substr(0, 10),
      menu2: false,
      menu1: false,
      finalDate: null,
      movementConcept: "",
      movementComment: "",
      movementFreq: undefined,
      freqUnit: "",
      movementAmount: undefined,
      movementType: "Gasto",
      movementCategory: undefined,
      submitStatus: null,
    };
  },
  methods: {
    switchG() {
      this.color1 = "#445e73";
      this.color2 = "#13314a";
    },
    switchI() {
      this.color2 = "#445e73";
      this.color1 = "#13314a";
    },
    switchU() {
      this.color3 = "#445e73";
      this.color4 = "#13314a";
      this.isUnique = true;
    },
    switchP() {
      this.color4 = "#445e73";
      this.color3 = "#13314a";
      this.isUnique = false;
    },
    confirm() {
      this.$v.$touch();
      if (this.isInvalid()) {
        console.log("error");
        this.submitStatus = "ERROR";
      } else if (this.isUnique) {
        this.submitStatus = "PENDING";
        this.createUniqueMovement();
        setTimeout(() => {
          this.submitStatus = "OK";
        }, 500);
      } else if (!this.isUnique) {
        this.submitStatus = "PENDING";
        this.createPeriodicMovement();
        setTimeout(() => {
          this.submitStatus = "OK";
        }, 500);
      }
    },
    createUniqueMovement() {
      var movement = {
        concept: this.movementConcept,
        amount: parseFloat(this.movementAmount),
        category: this.movementCategory,
        type: this.movementType,
        date: this.initialDate,
        comment: this.movementComment,
      };
      this.addUniqueMovement(movement);
      this.clear();
    },
    createPeriodicMovement() {
      var movement = {
        concept: this.movementConcept,
        amount: parseFloat(this.movementAmount),
        category: this.movementCategory,
        type: this.movementType,
        initialDate: this.initialDate,
        finalDate: this.finalDate,
        comment: this.movementComment,
        freq: this.movementFreq,
        freqUnit: this.freqUnit,
      };
      this.addPeriodicMovement(movement);
      if (this.initialDate === new Date().toISOString().substr(0, 10)) {
        var uniqueMovement = {
          concept: this.movementConcept,
          amount: parseFloat(this.movementAmount),
          category: this.movementCategory,
          type: this.movementType,
          date: this.initialDate,
          comment: this.movementComment,
        };
        this.addUniqueMovement(uniqueMovement);
      }
      this.clear();
    },
    clear() {
      this.$v.$reset();
      this.initialDate = new Date().toISOString().substr(0, 10);
      this.finalDate = null;
      this.movementConcept = "";
      this.movementComment = "";
      this.movementFreq = undefined;
      this.freqUnit = "";
      this.movementAmount = undefined;
      this.movementType = "Gasto";
      this.movementCategory = undefined;
      this.pressedG = true;
      this.pressedI = false;
      this.pressedU = true;
      this.pressedP = false;
      this.switchG();
      this.switchU();
    },
    isInvalid() {
      var inv =
        this.$v.movementConcept.$invalid ||
        this.$v.movementAmount.$invalid ||
        this.$v.movementCategory.$invalid ||
        this.$v.movementType.$invalid ||
        this.$v.initialDate.$invalid;
      if (this.isUnique) {
        return inv;
      } else {
        return (
          inv ||
          this.$v.finalDate.$invalid ||
          this.$v.movementFreq.$invalid ||
          this.$v.freqUnit.$invalid
        );
      }
    },
    ...mapMutations(["addUniqueMovement", "addPeriodicMovement"]),
  },
  computed: {
    conceptErrors() {
      const errors = [];
      if (!this.$v.movementConcept.$dirty) return errors;
      !this.$v.movementConcept.maxLength &&
        errors.push("El concepto debe ocupar como máximo 20 caracteres.");
      !this.$v.movementConcept.required && errors.push("Campo requerido.");
      return errors;
    },
    amountErrors() {
      const errors = [];
      if (!this.$v.movementAmount.$dirty) return errors;
      !this.$v.movementAmount.decimal &&
        errors.push("El importe debe ser numérico.");
      !this.$v.movementAmount.required && errors.push("Campo requerido.");
      !this.$v.movementAmount.minAmount &&
        errors.push("El importe debe ser superior a 0€.");
      !this.$v.movementAmount.twoDecimals &&
        errors.push("El importe debe tener como máximo 2 decimales.");
      return errors;
    },
    frequencyErrors() {
      const errors = [];
      if (this.isUnique) {
        return errors;
      }
      if (!this.$v.movementFreq.$dirty) return errors;
      !this.$v.movementFreq.integer &&
        errors.push("La frecuencia debe ser un número natural");
      !this.$v.movementFreq.required && errors.push("Campo requerido.");
      !this.$v.movementFreq.minFreq &&
        errors.push("La frecuencia debe ser superior a 1.");
      return errors;
    },
    categoryErrors() {
      const errors = [];
      if (!this.$v.movementCategory.$dirty) return errors;
      !this.$v.movementCategory.required && errors.push("Campo requerido.");
      return errors;
    },
    typeErrors() {
      const errors = [];
      if (!this.$v.movementType.$dirty) return errors;
      !this.$v.movementType.required && errors.push("Campo requerido.");
      return errors;
    },
    initialDateErrors() {
      const errors = [];
      if (!this.$v.initialDate.$dirty) return errors;
      !this.$v.initialDate.required && errors.push("Campo requerido.");
      return errors;
    },
    finalDateErrors() {
      const errors = [];
      if (!this.$v.finalDate.$dirty) return errors;
      !this.$v.finalDate.isAfter &&
        errors.push("La fecha de fin debe ser posterior a la de inicio.");
      return errors;
    },
    freqUnitErrors() {
      const errors = [];

      if (this.isUnique) {
        return errors;
      }
      if (!this.$v.freqUnit.$dirty) return errors;
      !this.$v.freqUnit.required && errors.push("Campo requerido.");
      return errors;
    },
  },
};
</script>
